body {
    background-color: black;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
  }
}

.App-header {
  background-color: #000000;
  min-height: 50vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(3px + 2vmin);
  color: white;
}

.App-link {
  font-size: 15px;
  color: #61dafb;
}

.message {
    font-family: 'Bungee Inline', cursive;
    text-align: center;
    width: 30ch;
    white-space: nowrap;
    overflow: hidden;
    animation: typing 2s steps(26, end);
    animation: flicker 1.5s infinite alternate;
}

@keyframes typing {
    from {
        width: 0;
    }
}

.story {
    font-family: 'Bungee Inline', cursive;
    animation: flicker 1.5s infinite alternate;
}

ul {
    text-align: left;
}

@keyframes flicker {
    0%, 8%, 12%, 15%, 33%, 37%, 10% {
      text-shadow:
        0 0 2px #fff,
        0 0 5px #fff,
        // 0 0 7px #fff,
        // 0 0 10px #0fa,
        // 0 0 15px #0fa,
        // 0 0 20px #0fa,
        0 0 6px rgb(172, 224, 60),
        0 0 10px rgb(213, 250, 77);
    }
    10%, 14%, 35% {       
      text-shadow: none;
    }
  }